import Vue from 'vue';
import {
    CellGroup,
    Cell,
    Button,
    Field,
    Tab,
    Tabs,
    Popup,
    Picker,
    Image,
    Icon,
    Row,
    Col,
    ImagePreview,
    Toast,
    Notify,
    Dialog,
    DropdownMenu,
    DropdownItem,
    ActionSheet,
    Grid,
    GridItem,
    Form,
    Tabbar,
    TabbarItem,
    Lazyload,
    Search,
    Radio,
    RadioGroup,
    IndexBar,
    IndexAnchor,
    TreeSelect

} from 'vant';

Vue.use(ActionSheet);
Vue.use(CellGroup);
Vue.use(Cell);
Vue.use(Button);
Vue.use(Field);
Vue.use(Tab);
Vue.use(Tabs);
Vue.use(Popup);
Vue.use(Picker);
Vue.use(Image);
Vue.use(Icon);
Vue.use(Row);
Vue.use(Col);
Vue.use(DropdownMenu)
Vue.use(DropdownItem)
Vue.use(Toast);
Vue.use(Notify);
Vue.use(Dialog);
Vue.use(ImagePreview);
Vue.use(Grid);
Vue.use(GridItem);
Vue.use(Form);
Vue.use(Tabbar);
Vue.use(TabbarItem);
Vue.use(Lazyload);
Vue.use(Search);
Vue.use(Radio);
Vue.use(RadioGroup);
Vue.use(IndexBar);
Vue.use(IndexAnchor);
Vue.use(TreeSelect);

Vue.prototype.$loading = {
    service: Toast.loading,

    open(message) {
        if (Vue.prototype.$loadingService) {
            Vue.prototype.$loadingService.clear();
        }
        Vue.prototype.$loadingService = Toast.loading({
            duration: 0,
            forbidClick: true,
            message,
        });
    },

    close(message) {
        setTimeout(
            () => {
                if (Vue.prototype.$loadingService) {
                    Vue.prototype.$loadingService.clear();
                    Vue.prototype.$loadingService = null;
                }
                if (message) {
                    Notify(message);
                }
            },
            500,
            this,
        );
    },
};

Vue.prototype.$toast = Toast;
Vue.prototype.$notify = Notify;
Vue.prototype.$imagePreview = ImagePreview;
Vue.prototype.$dialog = Dialog;
